.app__footer {
  width: 100%;
  position: relative;
  z-index: 1;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
  background: var(--color-black);
  padding-top: 0;
}
.app__footer-links {
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: flex-start;
  margin-top: 5rem;
  padding: 0 2rem;
  z-index: 3;
}
.app__footer-links_contact,
.app__footer-links_logo,
.app__footer-links_work {
  flex: 1;
  margin: 1rem;
  text-align: center;
}
.app__footer-links_logo img:first-child {
  width: 210px;
  margin-bottom: 0.75rem;
}
.app__footer-links_work p:nth-child(2n + 1) {
  margin-bottom: 1rem;
}
.app__footer-links_icons {
  margin-top: 0.75rem;
}
.app__footer-links_icons svg {
  color: var(--color-golden);
  margin: 0.8rem;
  font-size: 25px;
  cursor: pointer;
}
.app__footer-links_icons svg:hover {
  color: white;
}
.app__footer-headtext {
  font-family: var(--font-base);
  color: white;
  font-weight: 400;
  letter-spacing: 0.04em;
  text-transform: capitalize;
  font-size: 32px;
  line-height: 40px;
  margin-bottom: 1rem;
}
@media screen and (max-width: 1150px) {
  .app__footer-links {
    flex-direction: column;
    align-items: center;
  }
}
@media screen and (max-width: 990px) {
  .app__footer-links_contact,
  .app__footer-links_logo,
  .app__footer-links_work {
    margin: 2rem 0;
    width: 100%;
  }
}
@media screen and (max-width: 650px) {
  .app__footer {
    padding: 0 0 2rem 0;
  }
}
@media screen and (max-width: 350px) {
  .app__footer-links_logo img:first-child {
    width: 80%;
  }
}
