.app__specialMenu {
  flex-direction: column;
  background: var(--color-black);
}
.app__specialMenu-title {
  margin-bottom: 2rem;
  text-align: center;
}
.app__specialMenu-menu {
  width: 100%;
  margin: 2rem 0;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: row;
}
.app__specialMenu-menu_heading {
  font-family: var(--font-base);
  font-weight: 600;
  font-size: 45px;
  line-height: 60px;
  color: var(--color-white);
  margin-bottom: 2rem;
}
.app__specialMenu-menu_wine,
.app__specialMenu-menu_cocktails {
  flex: 1;
  width: 100%;
  flex-direction: column;
}
.app__specialMenu-menu_img {
  width: 410px;
  margin: 0 2rem;
}
.app__specialMenu-menu_img img {
  width: 100%;
  height: auto;
}
.app__specialMenu_menu_items {
  display: flex;
  flex-direction: column;
  margin: 2rem 0;
  width: 100%;
}
.app__button {
  margin-bottom: 3rem;
}
@media screen and (max-width: 1150px) {
  .app__specialMenu-menu {
    flex-direction: column;
    align-items: center;
    width: 100%;
  }
  .app__specialMenu-menu_img {
    margin: 1rem 0;
  }
  .app__button {
    margin-bottom: 2rem;
  }
}
@media screen and (max-width: 650px) {
  .app__specialMenu-menu_img {
    width: 100%;
    justify-content: center;
    align-items: center;
    padding-left: 4rem;
  }
  .app__specialMenu-menu_img img {
    width: 80%;
  }
  .app__specialMenu-menu_heading {
    font-size: 35px;
    line-height: 48px;
  }
  .app__specialMenu_menu_items {
    padding-left: 3rem;
  }
}
